import {
  useFinanceFeeStructureDetail,
  useFinanceSocietyMarkHead,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInputView from "../../../../Ui/Input/FormInputView";
import Modal from "../../../../Ui/Modal/Modal";
import { useTranslation } from "react-i18next";
import style from "../FeesStructure.module.css";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useState } from "react";
import FeeStructureDetailMenu from "./FeeStructureDetailMenu";
import { slashDateFormat } from "../../../../Utils/Functions/dateUtility";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";

const FeeStructureDetail = ({
  structureId,
  onClose,
  instituteId,
  fid,
  did,
  onRefetch,
  isView,
}) => {
  const { t } = useTranslation();
  const [openMenu, setOpenMenu] = useState("");
  const [disabled, setDisabled] = useState(false);
  const { financeFeeStructureDetail, financeFeeStructureDetailRefetch } =
    useFinanceFeeStructureDetail({
      fsid: structureId,
      skip: !structureId,
    });
  const [financeSocietyMarkHead] = useFinanceSocietyMarkHead();
  let totalHead = 0;
  let totalGovtHead = 0;
  if (financeFeeStructureDetail?.structure?.applicable_fees_heads?.length > 0) {
    financeFeeStructureDetail?.structure?.applicable_fees_heads?.map(
      (value) => (totalHead += value?.head_amount)
    );
  }
  if (financeFeeStructureDetail?.structure?.government_fees_heads?.length > 0) {
    financeFeeStructureDetail?.structure?.government_fees_heads?.map(
      (value) => (totalGovtHead += value?.head_amount)
    );
  }

  const onMenu = () => {
    if (financeFeeStructureDetail?.structure)
      setOpenMenu(() => financeFeeStructureDetail?.structure);
  };

  const onCloseEdit = () => {
    setOpenMenu("");
  };

  const onMarkAsSociety = (data, flow) => {
    if (structureId && data?._id) {
      setDisabled((pre) => !pre);
      financeSocietyMarkHead({
        fsid: structureId,
        bodyParameter: {
          flow: flow,
          fhid: data?._id,
          status: !data?.is_society,
        },
      })
        .then(() => {
          financeFeeStructureDetailRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      {disabled && <QvipleLoader />}
      {openMenu ? (
        <FeeStructureDetailMenu
          onClose={onCloseEdit}
          openMenu={openMenu}
          instituteId={instituteId}
          fid={fid}
          did={did}
          flowAs="Finance_Manager"
          onCloseParent={onClose}
          onRefetch={onRefetch}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("fee_structure")}</h6>
                </div>
                {isView === "STUDENT" ? null : (
                  <img
                    src={`${assestsUserFinanceUrl}/edit.svg`}
                    alt="close icon"
                    onClick={onMenu}
                  />
                )}
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInputView
                label={`${t("fee_structure_name")}: `}
                value={
                  financeFeeStructureDetail?.structure?.unique_structure_name ??
                  ""
                }
              />

              <FormInputView
                label={`${t("batch")}: `}
                value={
                  financeFeeStructureDetail?.structure?.batch_master?.batchName
                }
              />
              <FormInputView
                label={`${t("fees_category_name")}`}
                value={
                  financeFeeStructureDetail?.structure?.category_master
                    ?.category_name ?? ""
                }
              />
              <FormInputView
                label={`${t("standard_master")}`}
                value={
                  financeFeeStructureDetail?.structure?.class_master
                    ?.className ?? ""
                }
              />

              <section className={style.fees_head_title}>
                <h6>{t("total_fees")}</h6>
                <h6>
                  {t("rs_only")}{" "}
                  {financeFeeStructureDetail?.structure?.total_admission_fees ??
                    0}
                </h6>
              </section>

              <section className={style.fees_head_installment}>
                <h6
                  style={{
                    color: "#002BBE",
                  }}
                >
                  {t("applicable_fees_only_payable")} {" : "}
                </h6>
                <h6
                  style={{
                    color: "#002BBE",
                  }}
                >
                  {t("rs_only")}{" "}
                  {financeFeeStructureDetail?.structure?.applicable_fees ?? 0}
                </h6>
              </section>
              <section className={style.fees_head_installment}>
                <h6
                  style={{
                    color: "#002BBE",
                  }}
                >
                  {t("govt_scholarship")} {" : "}
                </h6>
                <h6
                  style={{
                    color: "#002BBE",
                  }}
                >
                  {t("rs_only")}{" "}
                  {(financeFeeStructureDetail?.structure
                    ?.total_admission_fees ?? 0) -
                    (financeFeeStructureDetail?.structure?.applicable_fees ??
                      0)}
                  {/* {financeFeeStructureDetail?.structure?.applicable_fees ?? 0} */}
                </h6>
              </section>
              {financeFeeStructureDetail?.structure?.one_installments?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.one_installments
                      ?.dueDate
                  }
                  label="first"
                  amount={
                    financeFeeStructureDetail?.structure?.one_installments?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.two_installments?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.two_installments
                      ?.dueDate
                  }
                  label="second"
                  amount={
                    financeFeeStructureDetail?.structure?.two_installments?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.three_installments
                ?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.three_installments
                      ?.dueDate
                  }
                  label="third"
                  amount={
                    financeFeeStructureDetail?.structure?.three_installments
                      ?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.four_installments?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.four_installments
                      ?.dueDate
                  }
                  label="fourth"
                  amount={
                    financeFeeStructureDetail?.structure?.four_installments
                      ?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.five_installments?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.five_installments
                      ?.dueDate
                  }
                  label="fifth"
                  amount={
                    financeFeeStructureDetail?.structure?.five_installments
                      ?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.six_installments?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.six_installments
                      ?.dueDate
                  }
                  label="sixth"
                  amount={
                    financeFeeStructureDetail?.structure?.six_installments?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.seven_installments
                ?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.seven_installments
                      ?.dueDate
                  }
                  label="seventh"
                  amount={
                    financeFeeStructureDetail?.structure?.seven_installments
                      ?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.eight_installments
                ?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.eight_installments
                      ?.dueDate
                  }
                  label="eight"
                  amount={
                    financeFeeStructureDetail?.structure?.eight_installments
                      ?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.nine_installments?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.nine_installments
                      ?.dueDate
                  }
                  label="ninth"
                  amount={
                    financeFeeStructureDetail?.structure?.nine_installments
                      ?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.ten_installments?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.ten_installments
                      ?.dueDate
                  }
                  label="ten"
                  amount={
                    financeFeeStructureDetail?.structure?.ten_installments?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.eleven_installments
                ?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.eleven_installments
                      ?.dueDate
                  }
                  label="eleventh"
                  amount={
                    financeFeeStructureDetail?.structure?.eleven_installments
                      ?.fees
                  }
                />
              ) : null}
              {financeFeeStructureDetail?.structure?.tweleve_installments
                ?.fees ? (
                <InstallmentCard
                  dueDate={
                    financeFeeStructureDetail?.structure?.tweleve_installments
                      ?.dueDate
                  }
                  label="twelevth"
                  amount={
                    financeFeeStructureDetail?.structure?.tweleve_installments
                      ?.fees
                  }
                />
              ) : null}

              <section
                className={style.table_container}
                style={{
                  margin: "0",
                }}
              >
                <div className={style.table_heading}>
                  <h6>{t("applicable_fee_heads")}</h6>
                  <h6>{t("amount")}</h6>
                </div>
                <div className={style.table_body}>
                  {financeFeeStructureDetail?.structure?.applicable_fees_heads?.map(
                    (feehead, index) => (
                      <div className={style.table_body_list} key={index}>
                        {isView === "STUDENT" ? (
                          <h6>{feehead?.head_name}</h6>
                        ) : (
                          <h6
                            style={{
                              display: "flex",
                              alignItems: "center",
                              justifyContent: "space-between",
                              gap: "1rem",
                            }}
                          >
                            {feehead?.head_name}
                            <img
                              src={
                                feehead?.is_society
                                  ? `${assestsUserAdmissionUrl}/check.svg`
                                  : `${assestsUserAdmissionUrl}/uncheck.svg`
                              }
                              alt="check icon"
                              onClick={() =>
                                onMarkAsSociety(feehead, "APPLICABLE")
                              }
                              style={{
                                cursor: "pointer",
                              }}
                            />
                            {/* <CreateButton
                              label={feehead?.is_society ? "unmark" : "mark"}
                              onAction={
                              }
                              customStyle={{
                                paddingBlock: "0.1rem",
                                paddingInline: "0.8rem",
                                width: "fit-content",
                                margin: "0",
                                fontSize: "14px",
                              }}
                            /> */}
                          </h6>
                        )}
                        <h6>{feehead?.head_amount}</h6>
                      </div>
                    )
                  )}
                </div>
                <div className={style.table_body}>
                  <div
                    className={style.table_body_list}
                    style={{
                      borderTopColor: "rgba(18, 18, 18, 0.8)",
                      borderTopWidth: "2px",
                      borderTopStyle: "solid",
                      borderBottom: "none",
                    }}
                  >
                    <h6 className={style.fees_total}>{t("subtotal")}</h6>
                    <h6 className={style.fees_total}>{totalHead}</h6>
                  </div>
                </div>
              </section>
              {totalGovtHead ? (
                <section
                  className={style.table_container}
                  style={{
                    margin: "0",
                  }}
                >
                  <div className={style.table_heading}>
                    <h6>{t("govt_fee_heads")}</h6>
                    <h6>{t("amount")}</h6>
                  </div>
                  <div className={style.table_body}>
                    {financeFeeStructureDetail?.structure?.government_fees_heads?.map(
                      (feehead, index) => (
                        <div className={style.table_body_list} key={index}>
                          {isView === "STUDENT" ? (
                            <h6>{feehead?.head_name}</h6>
                          ) : (
                            <h6
                              style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "space-between",
                                gap: "1rem",
                              }}
                            >
                              {feehead?.head_name}
                              <img
                                src={
                                  feehead?.is_society
                                    ? `${assestsUserAdmissionUrl}/check.svg`
                                    : `${assestsUserAdmissionUrl}/uncheck.svg`
                                }
                                alt="check icon"
                                onClick={() =>
                                  onMarkAsSociety(feehead, "GOVERNMENT")
                                }
                                style={{
                                  cursor: "pointer",
                                }}
                              />
                              {/* <CreateButton
                                label={feehead?.is_society ? "unmark" : "mark"}
                                onAction={() =>
                                  onMarkAsSociety(feehead, "GOVERNMENT")
                                }
                                customStyle={{
                                  paddingBlock: "0.1rem",
                                  paddingInline: "0.8rem",
                                  width: "fit-content",
                                  margin: "0",
                                  fontSize: "14px",
                                }}
                              /> */}
                            </h6>
                          )}

                          <h6>{feehead?.head_amount}</h6>
                        </div>
                      )
                    )}
                  </div>
                  <div className={style.table_body}>
                    <div
                      className={style.table_body_list}
                      style={{
                        borderTopColor: "rgba(18, 18, 18, 0.8)",
                        borderTopWidth: "2px",
                        borderTopStyle: "solid",
                        borderBottomColor: "rgba(18, 18, 18, 0.8)",
                        borderBottomWidth: "2px",
                      }}
                    >
                      <h6 className={style.fees_total}>{t("subtotal")}</h6>
                      <h6 className={style.fees_total}>{totalGovtHead}</h6>
                    </div>
                  </div>
                </section>
              ) : null}
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}
    </>
  );
};

export default FeeStructureDetail;

const InstallmentCard = ({ label, amount, dueDate }) => {
  const { t } = useTranslation();
  return (
    <section className={style.fees_head_installment}>
      <h6>
        {t(label)} {t("installment")}
        {dueDate ? (
          <span
            style={{
              fontWeight: "400",
              fontSize: "12px",
              marginLeft: "0.4rem",
            }}
          >
            ({`${slashDateFormat(dueDate)}`})
          </span>
        ) : (
          ""
        )}
      </h6>
      <h6>
        {t("rs_only")} {amount ?? 0}
      </h6>
    </section>
  );
};
