import { useEffect, useState } from "react";
import FeeStructureForm from "./FeeStructureForm";
import StructureAdmissionFee from "./StructureAdmissionFee";
import StructureFeeHead from "./StructureFeeHead";
import AddFeeStructurePreview from "./AddFeeStructurePreview";
import { useFinanceAddFeesStructure } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EditFeeStructureAs from "./EditFeeStructureAs";

const AddFeeStructure = ({
  onClose,
  did,
  fid,
  instituteId,
  openAs,
  onEdit,
  openStructureMenu,
  onRefetch,
  isRefetch,
}) => {
  const [step, setStep] = useState(0);
  const [structure, setStructure] = useState({
    structure_name: "",
    category_master: "",
    class_master: "",
    batch_master: "",
    total_admission_fees: "",
    applicable_fees: "",
    total_installments: "",
  });
  const [allInstallment, setAllInstallment] = useState([]);
  const [feeHeadAmount, setFeeHeadAmount] = useState(0);
  const [financeAddFeesStructure] = useFinanceAddFeesStructure();
  const [disabled, setDisabled] = useState(false);
  const [applicableFeeHead, setApplicableFeeHead] = useState([]);
  const [govtFeeHead, setGovtFeeHead] = useState([]);
  const [govtFeeHeadAmount, setGovtFeeHeadAmount] = useState(0);
  const [applicableFeeHeadId, setApplicableFeeHeadId] = useState([]);
  const [govtFeeHeadId, setGovtFeeHeadId] = useState([]);
  const [editType, setEditType] = useState("");

  useEffect(() => {
    if (openAs === "EDIT") {
      setStep(1);
      setStructure((prev) => ({
        ...prev,
        structure_name: openStructureMenu?.structure_name ?? "",
        category_master: openStructureMenu?.category_master ?? "",
        class_master: openStructureMenu?.class_master ?? "",
        batch_master: openStructureMenu?.batch_master ?? "",
        total_admission_fees: openStructureMenu?.total_admission_fees ?? "",
        applicable_fees: openStructureMenu?.applicable_fees ?? "",
        total_installments: openStructureMenu?.total_installments ?? "",
      }));
      let aHead = [];
      let amo = 0;
      for (let head of openStructureMenu?.applicable_fees_heads) {
        let obj = {
          created_at: head?.created_at,
          head_amount: head?.head_amount,
          head_name: head?.head_name,
          master: head?.master,
          _id: head?._id,
        };
        aHead.push(obj);
        amo += head?.head_amount;
      }
      setApplicableFeeHead(aHead);
      setFeeHeadAmount(amo);

      let gHeads = [];
      let gamo = 0;
      for (let head of openStructureMenu?.government_fees_heads) {
        let obj = {
          created_at: head?.created_at,
          head_amount: head?.head_amount,
          head_name: head?.head_name,
          master: head?.master,
          _id: head?._id,
        };
        gHeads.push(obj);
        amo += head?.head_amount;
      }
      setGovtFeeHead(gHeads);
      setGovtFeeHeadAmount(gamo);
      let install = getAllInstallment(openStructureMenu);
      setAllInstallment(install);
    }
  }, [openStructureMenu]);
  const onCreate = (asEdit) => {
    let combineFeeHead = [];
    if (applicableFeeHead?.length > 0) {
      combineFeeHead.push(...applicableFeeHead);
    }
    if (govtFeeHead?.length > 0) {
      combineFeeHead.push(...govtFeeHead);
    }
    if (openAs === "EDIT") {
      onEdit(
        {
          ...structure,
          structure_name: structure?.structure_name,
          category_master: structure?.category_master?._id,
          class_master: structure?.class_master?._id,
          batch_master: structure?.batch_master?._id,
          total_admission_fees: structure?.total_admission_fees
            ? structure?.total_admission_fees
            : 0,
          applicable_fees: structure?.applicable_fees
            ? structure?.applicable_fees
            : 0,
          total_installments: structure?.total_installments
            ? structure?.total_installments
            : 0,
          heads: combineFeeHead,
        },
        asEdit
      );
    } else {
      if (fid && did) {
        setDisabled((pre) => !pre);
        financeAddFeesStructure({
          fid: fid,
          flow: "Finance_Manager",
          createFeesStructure: {
            ...structure,
            structure_name: structure?.structure_name,
            category_master: structure?.category_master?._id,
            class_master: structure?.class_master?._id,
            batch_master: structure?.batch_master?._id,
            total_admission_fees: structure?.total_admission_fees
              ? structure?.total_admission_fees
              : 0,
            applicable_fees: structure?.applicable_fees
              ? structure?.applicable_fees
              : 0,
            total_installments: structure?.total_installments
              ? structure?.total_installments
              : 0,
            heads: combineFeeHead,
            did: did,
          },
        })
          .then(() => {
            if (isRefetch) onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch(() => {
            setDisabled((pre) => !pre);
          });
      }
    }
  };

  const onEditType = () => {
    setStep(4);
  };
  const onEditActionAs = (val) => {
    if (val) onCreate(val);
  };
  // console.info("openStructureMenu", step);
  return (
    <>
      {step === 0 ? (
        <FeeStructureForm
          onClose={onClose}
          did={did}
          fid={fid}
          instituteId={instituteId}
          structure={structure}
          setStructure={setStructure}
          setStep={setStep}
        />
      ) : step === 1 ? (
        <StructureAdmissionFee
          structure={structure}
          setAllInstallment={setAllInstallment}
          setStructure={setStructure}
          setStep={setStep}
          allInstallment={allInstallment}
          openAs={openAs}
          onParentClose={onClose}
        />
      ) : step === 2 ? (
        <StructureFeeHead
          structure={structure}
          setStep={setStep}
          setFeeHeadAmount={setFeeHeadAmount}
          feeHeadAmount={feeHeadAmount}
          allInstallment={allInstallment}
          fid={fid}
          applicableFeeHead={applicableFeeHead}
          govtFeeHead={govtFeeHead}
          setApplicableFeeHead={setApplicableFeeHead}
          setGovtFeeHead={setGovtFeeHead}
          setGovtFeeHeadAmount={setGovtFeeHeadAmount}
          govtFeeHeadAmount={govtFeeHeadAmount}
          setApplicableFeeHeadId={setApplicableFeeHeadId}
          applicableFeeHeadId={applicableFeeHeadId}
          govtFeeHeadId={govtFeeHeadId}
          setGovtFeeHeadId={setGovtFeeHeadId}
        />
      ) : step === 3 ? (
        <AddFeeStructurePreview
          structure={structure}
          applicableFeeHead={applicableFeeHead}
          govtFeeHead={govtFeeHead}
          feeHeadAmount={feeHeadAmount}
          govtFeeHeadAmount={govtFeeHeadAmount}
          allInstallment={allInstallment}
          setStep={setStep}
          onClose={() => setStep(2)}
          onCreate={openAs === "EDIT" ? onEditType : onCreate}
          openAs={openAs}
        />
      ) : step === 4 ? (
        <EditFeeStructureAs
          onEditActionAs={onEditActionAs}
          onClose={() => setStep(3)}
          setEditType={setEditType}
          editType={editType}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddFeeStructure;

const getAllInstallment = (openStructureMenu) => {
  let install = [];
  if (openStructureMenu?.one_installments?.fees) {
    install.push({
      fees: openStructureMenu?.one_installments?.fees,
      dueDate: openStructureMenu?.one_installments?.dueDate,
    });
  }
  if (openStructureMenu?.two_installments?.fees) {
    install.push({
      fees: openStructureMenu?.two_installments?.fees,
      dueDate: openStructureMenu?.two_installments?.dueDate,
    });
  }
  if (openStructureMenu?.three_installments?.fees) {
    install.push({
      fees: openStructureMenu?.three_installments?.fees,
      dueDate: openStructureMenu?.three_installments?.dueDate,
    });
  }
  if (openStructureMenu?.four_installments?.fees) {
    install.push({
      fees: openStructureMenu?.four_installments?.fees,
      dueDate: openStructureMenu?.four_installments?.dueDate,
    });
  }
  if (openStructureMenu?.five_installments?.fees) {
    install.push({
      fees: openStructureMenu?.five_installments?.fees,
      dueDate: openStructureMenu?.five_installments?.dueDate,
    });
  }
  if (openStructureMenu?.six_installments?.fees) {
    install.push({
      fees: openStructureMenu?.six_installments?.fees,
      dueDate: openStructureMenu?.six_installments?.dueDate,
    });
  }
  if (openStructureMenu?.seven_installments?.fees) {
    install.push({
      fees: openStructureMenu?.seven_installments?.fees,
      dueDate: openStructureMenu?.seven_installments?.dueDate,
    });
  }
  if (openStructureMenu?.eight_installments?.fees) {
    install.push({
      fees: openStructureMenu?.eight_installments?.fees,
      dueDate: openStructureMenu?.eight_installments?.dueDate,
    });
  }
  if (openStructureMenu?.nine_installments?.fees) {
    install.push({
      fees: openStructureMenu?.nine_installments?.fees,
      dueDate: openStructureMenu?.nine_installments?.dueDate,
    });
  }
  if (openStructureMenu?.ten_installments?.fees) {
    install.push({
      fees: openStructureMenu?.ten_installments?.fees,
      dueDate: openStructureMenu?.ten_installments?.dueDate,
    });
  }
  if (openStructureMenu?.eleven_installments?.fees) {
    install.push({
      fees: openStructureMenu?.eleven_installments?.fees,
      dueDate: openStructureMenu?.eleven_installments?.dueDate,
    });
  }
  if (openStructureMenu?.tweleve_installments?.fees) {
    install.push({
      fees: openStructureMenu?.tweleve_installments?.fees,
      dueDate: openStructureMenu?.tweleve_installments?.dueDate,
    });
  }
  return install;
};
