import BorderBottom from "../../../../Ui/Border/BorderBottom";
import SetButton from "../../../../Ui/Button/SetButton";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useTranslation } from "react-i18next";
import style from "../FeesStructure.module.css";
import { useEffect, useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import SelectMasterFeeHead from "./SelectMasterFeeHead";
import NotifyEvent from "../../../../Utils/Snackbar/NotifyEvent";
import { slashDateFormat } from "../../../../Utils/Functions/dateUtility";

const StructureFeeHead = ({
  structure,
  setStep,
  setFeeHeadAmount,
  feeHeadAmount,
  allInstallment,
  fid,
  applicableFeeHead,
  govtFeeHead,
  setApplicableFeeHead,
  setGovtFeeHead,
  govtFeeHeadAmount,
  setGovtFeeHeadAmount,

  setApplicableFeeHeadId,
  applicableFeeHeadId,
  govtFeeHeadId,
  setGovtFeeHeadId,
}) => {
  const { t } = useTranslation();
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [totalFeeHeadAmount, setTotalFeeHeadAmount] = useState(
    feeHeadAmount ?? 0
  );
  const [addApplicableFeeHead, setAddApplicableFeeHead] = useState(
    applicableFeeHead ?? []
  );
  const [addGovtFeeHead, setAddGovtFeeHead] = useState(govtFeeHead ?? []);
  const [totalGovt, setTotalGovt] = useState(govtFeeHeadAmount ?? 0);
  const [open, setOpen] = useState("");
  useEffect(() => {
    if (addApplicableFeeHead) {
      let total = 0;
      for (let i = 0; i < addApplicableFeeHead?.length; i++) {
        total += +addApplicableFeeHead[i]?.head_amount;
      }
      setTotalFeeHeadAmount(total);
    }
  }, [addApplicableFeeHead]);
  useEffect(() => {
    if (addGovtFeeHead) {
      let total = 0;
      for (let i = 0; i < addGovtFeeHead?.length; i++) {
        total += +addGovtFeeHead[i]?.head_amount;
      }
      setTotalGovt(total);
    }
  }, [addGovtFeeHead]);

  const onFeeHeadPriceChange = (e, index) => {
    const input1 = [...addApplicableFeeHead];
    for (let inp = 0; inp < input1?.length; inp++) {
      if (inp === index) {
        input1[inp].head_amount = +e.target.value;
      }
    }
    setAddApplicableFeeHead(input1);
  };

  const onFeeHeadLabelChange = (e, index) => {
    const input1 = [...addApplicableFeeHead];
    for (let inp = 0; inp < input1?.length; inp++) {
      if (inp === index) {
        input1[inp].head_name = e.target.value;
      }
    }
    setAddApplicableFeeHead(input1);
  };
  const onGovtFeeHeadPriceChange = (e, index) => {
    const input1 = [...addGovtFeeHead];
    for (let inp = 0; inp < input1?.length; inp++) {
      if (inp === index) {
        input1[inp].head_amount = +e.target.value;
      }
    }
    setAddGovtFeeHead(input1);
  };

  const onGovtFeeHeadLabelChange = (e, index) => {
    const input1 = [...addGovtFeeHead];
    for (let inp = 0; inp < input1?.length; inp++) {
      if (inp === index) {
        input1[inp].head_name = e.target.value;
      }
    }
    setAddGovtFeeHead(input1);
  };
  const onSetFeeHead = () => {
    let schFees =
      (structure?.total_admission_fees ?? 0) -
      (structure?.applicable_fees ?? 0);
    if (totalFeeHeadAmount > structure?.applicable_fees) {
      setSnackbar({
        msg: "You can't enter amount greater than applicable fees",
        open: true,
        variant: "error",
      });
    } else if (totalGovt > schFees) {
      setSnackbar({
        msg: "You can't enter amount greater than govt./scholarship fees",
        open: true,
        variant: "error",
      });
    } else {
      setApplicableFeeHead(addApplicableFeeHead);
      setGovtFeeHead(addGovtFeeHead);
      setFeeHeadAmount(totalFeeHeadAmount);
      setGovtFeeHeadAmount(totalGovt);
      setStep(3);
    }
  };

  const onAddButton = () => {
    setOpen("");
  };
  return (
    <>
      {open === "BY_APPLICABLE" ? (
        <SelectMasterFeeHead
          fid={fid}
          onClose={onAddButton}
          setFeeHead={setAddApplicableFeeHead}
          setFeeHeadId={setApplicableFeeHeadId}
          feeHeadId={applicableFeeHeadId}
          isSegment="BY_APPLICABLE"
        />
      ) : open === "BY_GOVERNMENT" ? (
        <SelectMasterFeeHead
          fid={fid}
          onClose={onAddButton}
          setFeeHead={setAddGovtFeeHead}
          setFeeHeadId={setGovtFeeHeadId}
          feeHeadId={govtFeeHeadId}
          isSegment="BY_GOVERNMENT"
        />
      ) : (
        <Modal>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("fee_heads")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => setStep(1)}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <section className={style.fees_head_title}>
                <h6>{t("total_fees")}</h6>
                <h6>
                  {t("rs_only")} {structure?.total_admission_fees ?? 0}
                </h6>
              </section>
              <section className={style.fees_head_installment}>
                <h6
                  style={{
                    color: "#002BBE",
                  }}
                >
                  {t("applicable_fees_only")}
                </h6>
                <h6
                  style={{
                    color: "#002BBE",
                  }}
                >
                  {t("rs_only")} {structure?.applicable_fees ?? 0}
                </h6>
              </section>
              <section className={style.fees_head_installment}>
                <h6
                  style={{
                    color: "#002BBE",
                  }}
                >
                  {t("govt_scholarship")} {" : "}
                </h6>
                <h6
                  style={{
                    color: "#002BBE",
                  }}
                >
                  {t("rs_only")}{" "}
                  {(structure?.total_admission_fees ?? 0) -
                    (structure?.applicable_fees ?? 0)}
                </h6>
              </section>
              {allInstallment?.map((install, index) => (
                <section className={style.fees_head_installment} key={index}>
                  <h6
                    style={{
                      textTransform: "capitalize",
                    }}
                  >
                    {t(onWhichInstallment(index))} {t("installment")}
                    {install?.dueDate ? (
                      <span
                        style={{
                          fontWeight: "400",
                          fontSize: "12px",
                          marginLeft: "0.4rem",
                        }}
                      >
                        ({`${slashDateFormat(install?.dueDate)}`})
                      </span>
                    ) : (
                      ""
                    )}
                  </h6>
                  <h6>
                    {t("rs_only")} {install?.fees ?? 0}
                  </h6>
                </section>
              ))}

              <section
                className={style.table_container}
                style={{
                  margin: "0",
                }}
              >
                <div className={style.table_heading}>
                  <h6>{t("applicable_fee_heads")}</h6>
                  <h6>{t("amount")}</h6>
                </div>
                <div className={style.table_body}>
                  {addApplicableFeeHead?.map((afh, index) => (
                    <div className={style.table_body_list} key={index}>
                      <input
                        type={"text"}
                        value={afh?.head_name}
                        onChange={(e) => onFeeHeadLabelChange(e, index)}
                        className={style.fee_head_name_input}
                      />
                      <input
                        type={"number"}
                        value={afh?.head_amount}
                        onChange={(e) => onFeeHeadPriceChange(e, index)}
                        className={style.fee_head_amount_input}
                      />
                    </div>
                  ))}
                </div>
                <button
                  className={style.add_fees_head_btn}
                  title="Add Fees"
                  onClick={() => setOpen("BY_APPLICABLE")}
                  style={{
                    cursor: "pointer",
                  }}
                >
                  {t("add")}
                  <img src={`${assestsUserFinanceUrl}/plus.svg`} alt="add" />
                </button>
                <div className={style.table_body}>
                  <div
                    className={style.table_body_list}
                    style={{
                      borderTopColor: "rgba(18, 18, 18, 0.5)",
                      borderTopWidth: "0.5px",
                      borderTopStyle: "solid",
                      borderBottom: "none",
                    }}
                  >
                    <h6 className={style.fees_total}>{t("subtotal")}</h6>
                    <h6 className={style.fees_total}>{totalFeeHeadAmount}</h6>
                  </div>
                </div>
              </section>

              <section
                className={style.table_container}
                style={{
                  margin: "0",
                }}
              >
                <div className={style.table_heading}>
                  <h6>{t("govt_fee_heads")}</h6>
                  <h6>{t("amount")}</h6>
                </div>
                <div className={style.table_body}>
                  {addGovtFeeHead?.map((gfh, index) => (
                    <div className={style.table_body_list} key={index}>
                      <input
                        type={"text"}
                        value={gfh?.head_name}
                        onChange={(e) => onGovtFeeHeadLabelChange(e, index)}
                        className={style.fee_head_name_input}
                      />
                      <input
                        type={"number"}
                        value={gfh?.head_amount}
                        onChange={(e) => onGovtFeeHeadPriceChange(e, index)}
                        className={style.fee_head_amount_input}
                      />
                    </div>
                  ))}
                </div>
                <button
                  className={style.add_fees_head_btn}
                  title="Add Fees"
                  onClick={() => setOpen("BY_GOVERNMENT")}
                  style={{
                    cursor: "pointer",
                  }}
                >
                  {t("add")}
                  <img src={`${assestsUserFinanceUrl}/plus.svg`} alt="add" />
                </button>
                <div className={style.table_body}>
                  <div
                    className={style.table_body_list}
                    style={{
                      borderTopColor: "rgba(18, 18, 18, 0.5)",
                      borderTopWidth: "0.5px",
                      borderTopStyle: "solid",
                      borderBottomColor: "rgba(18, 18, 18, 0.8)",
                      borderBottomWidth: "2px",
                    }}
                  >
                    <h6 className={style.fees_total}>{t("subtotal")}</h6>
                    <h6 className={style.fees_total}>{totalGovt}</h6>
                  </div>
                </div>
              </section>

              <SetButton label="preview" onAction={onSetFeeHead} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default StructureFeeHead;
const onWhichInstallment = (val) => {
  if (val === 0) return "first";
  else if (val === 1) return "second";
  else if (val === 2) return "third";
  else if (val === 3) return "foutrh";
  else if (val === 4) return "fivth";
  else if (val === 5) return "sixth";
  else if (val === 6) return "seventh";
  else if (val === 7) return "eighth";
  else if (val === 8) return "ninth";
  else if (val === 9) return "ten";
  else if (val === 10) return "eleven";
  else return "tweleve";
};
